


































export default {
	data() {
		return {
			dc_info:'',
			img:''
		};
	},
	onLoad(option) {
		var that = this;
		uni.showLoading({
			title:'加载中...'
		})
		uni.request({
			url:that.http+'index/getDcInfo',
			header:that.header,
			method:'POST',
			data:{
				dc_id:option.id
			},
			success: (res) => {
				uni.hideLoading()
				console.log(res)
				that.dc_info = res.data.data
				that.img = res.data.data.pics_src
			},
			fail() {
				uni.showToast({
					title:'网络延迟，请刷新后重试!',
					icon:'none'
				})
			}
		})
	}
}
